        let name = this.$route.query.schoolid
        if(name=='浙江大学'){
            this.tempdata=this.zheda
        }else if(name=='北京大学'){
            this.tempdata=this.beida
        }else if(name=='武汉大学'){
            this.tempdata=this.wuda
        }else if(name=='郑州大学'){
            this.tempdata=this.zhengda
        }
        console.log(this.tempdata)
    },
    methods:{
        handleScroll(){
            let srcllTop=window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
            //显示第一个部分
            if(srcllTop>180&&this.showpart1==false){
                this.showpart1=true
            }
            if(srcllTop>900&&this.showpart2==false){
                this.showpart2=true
            }
            if(srcllTop>1550&&this.showpart3==false){
                this.showpart3=true
            }
            if(srcllTop>2350&&this.showpart4==false){
                this.showpart4=true
            }
            if(srcllTop>3000&&this.showpart5==false){
                this.showpart5=true
                //关闭定时器
                window.removeEventListener("scroll",this.handleScroll,true)
            }
            // console.log(srcllTop)
        }
    },
    mounted() {
        //监听页面滚动
        window.addEventListener("scroll",this.handleScroll,true)
    },
    beforeDestroy() {
        //关闭定时器
        window.removeEventListener("scroll",this.handleScroll,true)
    },
}
