    </transition>

    <Season class="season" :imgs="tempdata.season" :texts="tempdata.seasontext"></Season>

    <!-- 底部照片 -->

    <div class="bottomimgs">
    <transition
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeInRightBig"
    >    
    <Bottomimgs :alldata="tempdata.topimgs" :alltitle="tempdata.toptext.title" :alletitle="tempdata.toptext.etitle"></Bottomimgs>
    </transition>

    <transition
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeInLeftBig"
    >    
    <Bottomimgs :alldata="tempdata.bottomimgs" :alltitle="tempdata.bottomtext.title" :alletitle="tempdata.bottomtext.etitle"></Bottomimgs>
    </transition>
    </div>

</div>
