                        '空山新雨后,天气晚来秋。明月松间照,清泉石上流。   ——王维',
                        '千里黄天白日曛,北风吹雁雪纷纷。   ——高适',
                    ],
                    topimgs:[
                        require('./Scenery/imgs/school/zhejiang/8/垂柳.jpg'),
                        require('./Scenery/imgs/school/zhejiang/8/华家池.jpg'),
                        require('./Scenery/imgs/school/zhejiang/8/黄昏.jpg'),
                        require('./Scenery/imgs/school/zhejiang/8/老建筑.jpg')
                    ],
                    bottomimgs:[
                    require('./Scenery/imgs/school/zhejiang/8/木桥.jpg'),
                    require('./Scenery/imgs/school/zhejiang/8/青春.jpg'),
                    require('./Scenery/imgs/school/zhejiang/8/夜色.jpg'),
                    require('./Scenery/imgs/school/zhejiang/8/幽静.jpg')
                    ],
                    toptext:{
