                    }
                },
                wuda:{
                    name:'wuda',
                    season:[require('./Scenery/imgs/school/wuhan/wuhan四季/whspring.jpg'),
                    require('./Scenery/imgs/school/wuhan/wuhan四季/whsummer.jpg'),
                    require('./Scenery/imgs/school/wuhan/wuhan四季/whautumn.jpg'),
                    require('./Scenery/imgs/school/wuhan/wuhan四季/whwinter.jpg')
                    ],
