                    topimgs:[
                        require('./Scenery/imgs/school/wuhan/8/毕业典礼.jpg'),
                        require('./Scenery/imgs/school/wuhan/8/开学典礼.jpg'),
                        require('./Scenery/imgs/school/wuhan/8/老斋舍.jpg'),
                        require('./Scenery/imgs/school/wuhan/8/珞珈山路.jpg')
                    ],
                    bottomimgs:[
                    require('./Scenery/imgs/school/wuhan/8/青春.jpg'),
                    require('./Scenery/imgs/school/wuhan/8/体育馆.jpg'),
                    require('./Scenery/imgs/school/wuhan/8/晚霞.jpg'),
                    require('./Scenery/imgs/school/wuhan/8/夜晚.jpg')
                    ],
                    toptext:{
                        title:[
                            '毕业典礼',
                            '开学典礼',
                            '老斋舍',
                            '珞珈山路'
                        ],
                        etitle:[
                            'Opening ceremony',
                            'Graduation ceremony',
                            'Old zhai house',
                            'Luojiashan Road'
                        ]   
                    },
                    bottomtext:{
                        title:[
                            '青春',
                            '体育馆',
                            '晚霞',
                            '夜晚'
                        ],
                        etitle:[
                            'Youth',
                            'Gymnasium',
                            'Sunset glow',
                            'Night view'
                        ]   
                    }
                },
                zhengda:{
                    name:'zhengda',
                    season:[require('./Scenery/imgs/school/zzu/zzu四季/zzuspring.jpg'),
                    require('./Scenery/imgs/school/zzu/zzu四季/zzusummer.jpg'),
                    require('./Scenery/imgs/school/zzu/zzu四季/zzuautumn.jpg'),
                    require('./Scenery/imgs/school/zzu/zzu四季/zzuwinter.jpg')
                    ],
                    seasontext:[
                        '红豆生南国,春来发几枝   ——王维',
                        '景雨初过爽气清,玉波荡漾画桥平。   ——朱瞻基',
                        '晴空一鹤排云上,便引诗情到碧霄。   ——刘禹锡',
                        '平明走马上村桥,花落梅溪雪未消。   ——贾岛',
                    ],
                    topimgs:[
                        require('./Scenery/imgs/school/zzu/8/爱情.jpg'),
                        require('./Scenery/imgs/school/zzu/8/匆匆那年.jpg'),
                        require('./Scenery/imgs/school/zzu/8/花海.jpg'),
                        require('./Scenery/imgs/school/zzu/8/眉湖.jpg'),
                    ],
                    bottomimgs:[
                    require('./Scenery/imgs/school/zzu/8/眉湖小船.jpg'),
                    require('./Scenery/imgs/school/zzu/8/桃王.jpg'),
                    require('./Scenery/imgs/school/zzu/8/行政楼.jpg'),
                    require('./Scenery/imgs/school/zzu/8/钟楼.jpg')
                    ],
                    toptext:{
                        title:[
                            '爱情',
                            '匆匆那年',
                            '花海',
                            '眉湖'
                        ],
                        etitle:[
                            'Love',
                            'Rush year',
                            'Sea of flowers',
                            'Mei Lake'
                        ]   
                    },
                    bottomtext:{
                        title:[
                            '眉湖小船',
                            '桃王',
                            '行政楼',
                            '钟楼'
                        ],
                        etitle:[
                            'Mei Lake boat',
                            'Peach blossom king',
                            'Administration building',
                            'Bell tower'
                        ]   
                    }
                },
                beida:{
                    name:'beida',
                    season:[require('./Scenery/imgs/school/peking/peking四季/pekingspring.jpg'),
                    require('./Scenery/imgs/school/peking/peking四季/pekingsummer.jpg'),
                    require('./Scenery/imgs/school/peking/peking四季/pekingautumn.jpg'),
                    require('./Scenery/imgs/school/peking/peking四季/pekingwinter.jpg'),
                    ],
                    seasontext:[
                        '碧玉妆成一树高,万条垂下绿丝绦。   ——贺知章',
                        '溪涨清风拂满面,月落繁星满天。   ——陆游',
                        '夕照红于烧,晴空碧胜蓝。   ——白居易',
                        '桂密岩花白,梨疏树叶红。   ——王勃',
                    ],
                    topimgs:[
                        require('./Scenery/imgs/school/peking/8/倒影.jpg'),
                        require('./Scenery/imgs/school/peking/8/荷花.jpg'),
                        require('./Scenery/imgs/school/peking/8/绝美晚霞.jpg'),
                        require('./Scenery/imgs/school/peking/8/落日.jpg')
                    ],
                    bottomimgs:[
                    require('./Scenery/imgs/school/peking/8/秋色.jpg'),
                    require('./Scenery/imgs/school/peking/8/向往.jpg'),
                    require('./Scenery/imgs/school/peking/8/雪花飘飘.jpg'),
                    require('./Scenery/imgs/school/peking/8/夜色.jpg')
                    ],
                    toptext:{
                        title:[
                            '倒影',
                            '荷花',
                            '绝美晚霞',
                            '落日'
                        ],
                        etitle:[
                            'Inverted reflection in water',
                            'Lotus flower',
                            'A beautiful sunset glow',
                            'Sunset'
                        ]   
                    },
                    bottomtext:{
                        title:[
                            '秋色',
                            '向往',
                            '雪花飘飘',
                            '夜色'
                        ],
                        etitle:[
                            'Autumn color',
                            'Yearn for',
                            'Snowflakes are falling',
                            'Night view'
                        ]   
                    }
                }

            }
        }
    },
    components:{
        Shutter,
        Leftmenu,
        Bottomimgs,
        Season
    },
    //向组件传递数据
    beforeMount(){
        let name = this.$route.query.schoolid
        console.log('beforemount'+this.name)
        if(name=='浙江大学'){
            this.tempdata=this.alldata.zheda
        }else if(name=='北京大学'){
            this.tempdata=this.alldata.beida
        }else if(name=='武汉大学'){
            this.tempdata=this.alldata.wuda
        }else if(name=='郑州大学'){
            this.tempdata=this.alldata.zhengda
        }
    },
    mounted() {
        if(this.showShutter==false){
            this.showShutter=true
        }
        let that=this
        this.cycle=setInterval(() => {
            if(that.bg1==true){
                that.bg1=false
                setTimeout(() => {
                that.bg2=true
                }, 800);
            }
            else if(that.bg2==true){
                that.bg2=false
                setTimeout(() => {
                that.bg3=true
                }, 800);
            }
            else if(that.bg3==true){
                that.bg3=false
                setTimeout(() => {
                that.bg4=true
                }, 800);
            }
            else if(that.bg4==true){
                that.bg4=false
                setTimeout(() => {
                that.bg1=true
                }, 800);
            }
        }, 6000);    
    },
    beforeDestroy(){
        clearInterval(this.cycle)
    }
}
</script>
<style scoped>
.bottomimgs{
    position: absolute;
    left: 0px;
    bottom: 20px;
}
.bgdiv{
    opacity: 80%;
    position:fixed;
    z-index: -1;
    top: 60px;
    height: 692px;
    width: 100%;
    left: 40px;
}
.bgimg{
    width: 100%;
    height: 100%;
}
.schoolSceneryroot{
    height: 1950px;
    width: 100%;
    display: relative;
    overflow: hidden;
}
.season{
    margin-left: 3%;
    position: relative;
    margin-top: 10%;
}
</style>
