<template>
  <div>
    <!-- <div class="rollimagestext"> -->
        <transition
        v-if="showtitle"
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeInDown"
        >
          <span class="rollimagestext">
            开启你的大学之旅
          </span>
        </transition>
    <!-- </div> -->
  <el-carousel height="43em" :interval="4000">
    <el-carousel-item v-for="item in imgs" :key="item">
      <h3>
        <img :src="item" class="imgs">
      </h3>
    </el-carousel-item>
  </el-carousel>
</div>
</template>
<script>
//引入样式库
import 'animate.css';
export default{
    data() {
        return {
            //轮播图图片
            imgs:[
                'https://ts1.cn.mm.bing.net/th/id/R-C.cefd54933a7e79cc125eb0b845ed3d8b?rik=ZJUht%2bNTbE%2fPiw&riu=http%3a%2f%2ficucn.cucn.edu.cn%2fuploads%2f20200702%2fcefd54933a7e79cc125eb0b845ed3d8b.jpg&ehk=uT6O%2bsZLJN%2fyGI5xU868GcuabRxlPOp1uQjavbioeWU%3d&risl=&pid=ImgRaw&r=0',
                'https://www.gench.edu.cn/_upload/article/e8/4d/0195b8f3403aac675f0b3e760342/3228c789-4de4-4503-9adc-ca9a899b1e61.jpg',
                'https://news.cqu.edu.cn/uploadfile/2020/1120/1605867371303298.jpg'                
            ],
            showtitle:false
        }
    },
    mounted(){
      let that=this
      setTimeout(() => {
        if(that.showtitle==false){
          that.showtitle=true         
        }
      }, 500);
    }
}
</script>
<style>
  .el-carousel__item h3 {
    color: #475669;
    font-size: 18px;
    opacity: 0.75;
    line-height: 300px;
    margin: 0;
  }
  .imgs{
    height: 100%;
    width: 100%;
  }
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  .rollimagestext{
    position: absolute;
    top: 8em;
    align-items: center;
    width: 100%;
    text-align: center;
    z-index: 7;
    font-size: 4em;
    font-weight: 900;
    color: #eff5fe;
    letter-spacing: 15px;
    font-family:"楷体";
  }
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>