<template>
    <div>
    <transition
    appear
    name="animate__animated animate__bounce"
    enter-active-class="animate__zoomInDown"
    >
        <div class="card" v-if="showcard">
        <div class="inner">

            <div class="front">
                <div class="banner">
                    <img :src="schooldata.bgimg" alt="banner" width="100%">
                </div>
                <div class="profile alignment">
                    <img :src="schooldata.schoolIcon" alt="avatar" class="avatar" width="100px">
                    <h3 class="title">{{ schooldata.schollname }}</h3>
                    <h3 class="subtitle">{{ schooldata.eschollname }}</h3>
                </div>
            </div>

            <div class="back">
                <div class="alignment">
                    <p class="text">
                        {{schooldata.context}}
                    </p>
                    <div class="dividline">

                    </div>
                    <p class="address">
                        百度百科<br>{{schooldata.baidu}}
                    </p>
                </div>
            </div>

        </div>
    </div>
    </transition>
    <transition
    appear
    name="animate__animated animate__bounce"
    enter-active-class="animate__zoomInDown"
    >
    <img class="map" :src="schooldata.position" v-if="showmap">
    </transition>
    </div>
</template>
<script>
export default{
    name:'SchoolCards',
    props:['schooldata'],
    data() {
        return {
            showmap:false,
            showcard:false
        }
    },
    mounted(){
        let that=this
        setTimeout(() => {
            that.showcard=true          
        }, 500);
        setTimeout(() => {
            that.showmap=true
        }, 1000);
    }
    
}
</script>
<style scoped>
.map{
    width: 300px;
    height: 300px;
}
/* :root{
    --background: #e1e1e1;
    --color-light: rgb(82,82,82);
    --color-dark: rgb(128,128,128);
    --text-color-ight: #888;
}
body{
    background-color: var(--background);
    overflow: hidden;
    font-family: sans-serif;
} */
.dividline{
    height: 1px;
    width: 50%;
    border-top: dashed rgb(86, 86, 86) 1px;
    margin-top: 15px;
}
.alignment{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.card{
    width: 300px;
    height: 320px;
    perspective: 1000px;
    margin: 5% auto;
}
 
.card .inner{
    position: relative;
    width: 300px;
    height: 320px;
}
.front,
.back{ 
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    height: 100%;
    color: var(--text-color-ight);
    border-radius: 25px;
    box-shadow: 0 0 25px 2px rgba(0, 0, 0, 0.3);
    /* background: url('./img/300.jpg') 0 0 repeat; */
    background: url('./imags/bg.png') 0 0 repeat;
}
.back{
    backface-visibility: hidden;
}
.avatar{
    position: absolute;
    width: 6em;
    top: 60%;
    left: 50%;
    transform: translate(-50%,-50%);
    border-radius: 50%;
    border: 5px solid var(--background);
}
.banner+.profile{
    height: 50%;
    margin-top: 15px;
}
.title,.subtitle,.text,.address{
    margin-top: 0;
    margin-bottom: 0;
    text-align: center;
}
.subtitle,.address{
    font-size: 0.8em;
    font-weight: 400;
    text-transform: uppercase;
}
.text{
    margin: 20% 8% 0 8%;
}
.address::before{
    content: "";
    display: inline-block;
    border-top: 2px solid var(--text-color-ight);
    width: 100%;
    margin-bottom: 5px;
}
.back{
    transform: rotateY(180deg);
}
.card:hover .inner{
    transform: rotateY(180deg);
}
.inner{
    transition: transform 2s ease-in-out;
    transform-style: preserve-3d;
}
</style>