<template>
    <div class="root">
        <!-- 上边栏，和导航区做分割 -->
        <div class="topbar"></div>
        <!-- 轮播图组件 -->
        <Rollimages></Rollimages>
        <!-- 第一块信息内容 -->
        <Cardone v-if="showCardOne"></Cardone>
        <!-- 第二快内容 -->
        <Cardtwo v-if="showCardTwo"></Cardtwo>
        <!-- 第三块内容 -->
        <transition
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeInLeftBig"
        >
        <Cardthree v-if="showCardThree"></Cardthree>
        </transition>
        <transition
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeInLeftBig"
        >
        <!-- 学校s -->
        <Cards class="cards" v-if="showCards"></Cards>
        </transition>

        <!-- 底部 -->
        <Bottom v-if="showBottom"></Bottom>
    </div>
</template>
<script>
import Rollimages from './Mainpages/Rollimages.vue'
import Cardone from './Mainpages/Cardone.vue';
import Cardtwo from './Mainpages/Cardtwo.vue';
import Cardthree from './Mainpages/Cardthree.vue';
import Cards from './Mainpages/Cards.vue';
import Bottom from './Mainpages/Bottom.vue';
export default{
    data() {
        return {
            showCardOne:false,
            showCardTwo:false,
            showCardThree:false,
            showCards:false,
            showBottom:false
        }
    },
    components:{
        Rollimages,
        Cardone,
        Cards,
        Bottom,
        Cardtwo,
        Cardthree
    },
    methods:{
        //监听页面滚动
        handleScroll(){
            let srcllTop=window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
            //显示第一个部分
            if(srcllTop>140&&this.showCardOne==false){
                this.showCardOne=true
            }
            if(srcllTop>900&&this.showCardTwo==false){
                this.showCardTwo=true
            }
            if(srcllTop>1600&&this.showCardThree==false){
                this.showCardThree=true
            }
            if(srcllTop>2400&&this.showCards==false){
                this.showCards=true
            }
            if(srcllTop>3000&&this.showBottom==false){
                this.showBottom=true
                //关闭定时器
                window.removeEventListener("scroll",this.handleScroll,true)
            }
            //console.log(srcllTop)
        }
    },
    //生命周期挂载
    mounted(){
        //监听页面滚动
        window.addEventListener("scroll",this.handleScroll,true)
    },
        //生命周期销毁前
    beforeDestroy(){
        //移除监听页面滚动
        window.removeEventListener("scroll",this.handleScroll,true)
    }
}
</script>
<style scoped>
.cards{
    margin-top: 0;
    margin-left: auto;
    margin-right: auto;
    background-color: rgb(255, 255, 255);
}
.topbar{
    margin-top: 4em;
    height: 0em;
    width: 100%;
    background-color: rgb(255, 255, 255);
}
.root{
    width: 100%;
    height: 3800px; 
    position: relative;
}
</style>