<template>
<div class="showimgroot">
    <transition
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeIn animate__slower"
        leave-active-class="animate__fadeOut animate__fast"
    >
    <img class="img" :src="imgs[0]" v-if="showimg1">
    <img class="img" :src="imgs[1]" v-if="showimg2">
    </transition>

</div>
</template>
<script>

export default{
    //接受图片数据

    props:['imgs'],
    name:'Showimg',
    data() {
        return {
            showimg2:false,
            showimg1:true,
            circle1:'circled',
            circle2:'circle'
        }
    },
    methods:{
        changeimg(num){
            console.log(num)
            if(num==1){
                this.circle2='circle'
                this.circle1='circled'
            }
            if(num==2){
                this.circle1='circle'
                this.circle2='circled'
            }
            console.log(this.circle1,this.circle2)
        }
    },
    mounted() {
        let that=this 
        setInterval(() => {
            if(that.showimg1==false){
                that.showimg2=false
                setTimeout(() => {
                    that.showimg1=true
                }, 800);
            }else if(that.showimg2==false){
                setTimeout(() => {
                    that.showimg2=true
                }, 800);
                that.showimg1=false
            }            
        }, 8000);    
    },
}
</script>
