//整个项目的入口文件
import Vue from 'vue'
//引入App组件，所有组件的父组件
import App from './App.vue'
//引入注册的路由
import router from './router'
//引入组件库
import ElementUI from 'element-ui';
//引入组件样式
import 'element-ui/lib/theme-chalk/index.css';
//使用组件库
Vue.use(ElementUI);
//关闭Vue的生产提示
Vue.config.productionTip = false
//创建Vue实例对象(vm)
new Vue({
  render: h => h(App),
  // 生命周期，在创建之前
  beforeCreate(){
    //全局事件总线
    Vue.prototype.$bus=this
  },
  //使用路由
  router:router
}).$mount('#app')
