<style scoped>

.container{
    /* 弹性布局 水平垂直居中 */
    display: flex;
    margin-left: 400px;
    justify-content: center;
    align-items: center;
    width: 470px;
    height: 300px;
}

.card{
    /* 绝对定位 */
    position: absolute;
    background-color: #fff;
    width: 700px;
    height: 100px;
    /* 弹性布局 */
    display: flex;
    /* 将元素靠边对齐 */
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    border-radius: 100px 20px 20px 100px;
    opacity: 0;
    /* 执行动画:动画名称 时长 线性的 无限次播放 */
    animation: animate 15s linear infinite;
    /* 这里我们需要给每一个卡片设置一个不同的动画延迟时间 */
    /* 用到自定义属性,我们设置一个自定义属性--d */
    /* 通过var函数调用自定义属性--d,计算出动画延迟时间 */
    animation-delay:  calc(3s * var(--d));

}

.card .content{
    display: flex;
    align-items: center;
}

.card .img{
    width: 90px;
    height: 90px;
    position: absolute;
    left: 0;
    top: 0;
    background-color: #fff;
    padding: 5px;
    border-radius: 50%;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}
.card .img img{
    width: 100%;
    height: 100%;
    /* 对图片进行剪切,保留原始比例 */
    object-fit: cover;
    border-radius: 50%;
}

.card .detail{
    margin-left: 100px;

}

.card .detail span{
    display: block;
    font-size: 18px;
