<template>
<div class="wishPageroot">

    <transition
        appear
        name="animate__animated animate__bounce"
        enter-active-class="animate__fadeInDown"
        leave-active-class="animate__fadeOutUp"
      >
    <svg v-if="success" t="1681548859408" class="icon icons" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2604" width="200" height="200"><path d="M512 1024A512 512 0 1 1 512 0a512 512 0 0 1 0 1024z m-49.590857-377.197714L315.977143 498.614857 219.428571 590.848c70.217143 37.814857 168.594286 106.788571 252.854858 213.723429C531.821714 692.662857 715.337143 463.725714 804.571429 443.245714c-14.409143-57.709714-22.528-166.034286 0-223.817143-183.003429 120.685714-342.162286 427.373714-342.162286 427.373715z" fill="#029B00" p-id="2605"></path></svg>
    </transition>
    
    <div class="container">
        <div class="comment-box">
            <button class="btn-close">留下你的愿望吧!!!</button>
            <div class="comment-form">
                <div class="header">许愿板，记得回来还愿哦！</div>

                <div>
                    <input type="text" id="name" v-model="tempcontent.text.username" placeholder="输入您的姓名">
                </div>
                <!-- 选择框 -->
                <el-select v-model="selection" placeholder="请选择心仪大学" class="select">
                    <el-option
                    v-for="item in options"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                    </el-option>
                </el-select>

                <div>
                    <textarea name="" id="comment" v-model="tempcontent.text.wish" cols="30" rows="3" placeholder="输入您的愿望"></textarea>
                </div>
                <button id="btn-submit" @click="pushwish()">留下你的愿望</button>
            </div>
            <div>
                <h4 class="header">愿望列表</h4>
                <div id="comments">
                    <Wishs :alldata="content" v-if="showWishs"></Wishs>
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<script>
import Wishs from './WishPage/Wishs.vue';
export default{
    name:'WishPage',
    data() {
        return {
            selection:'',
            options: [{
          value: '郑州大学',
          label: '郑州大学'
        }, {
          value: '武汉大学',
          label: '武汉大学'
        }, {
          value: '北京大学',
          label: '北京大学'
        }, {
          value: '浙江大学',
          label: '浙江大学'
        },],
        success:false,
            showWishs:true,
            tempcontent:{
                keynum:0,
                keys:'--d:',
                text:{
                    username:'',
                    wish:'',
                },
                date:'',
                value:null
            },
            content:[
                {
                    keynum:-1,
                    keys:'--d:-1',
                    text:{
                        username:'张三',
                        wish:'我想上清华北大'
                    },
                    date:'2022.12.3',
                    value:require('../components/Mainpages/imags/wuhan.png')
                },
                {
                    keynum:0,
                    keys:'--d:0',
                    text:{
                        username:'李四',
                        wish:'拼搏白天我要上浙江大学'
                    },
                    date:'2022.11.01',
                    value:require('../components/Mainpages/imags/zhejiang2.png')
                },
                {
                    keynum:1,
                    keys:'--d:1',
                    text:{
                        username:'王五',
                        wish:'加油，不负韶华'
                    },
                    date:'2023.02.01',
                    value:require('../components/Mainpages/imags/zzu1.jpg')
                },
            ],
        }
    },
    components:{
        Wishs
    },
    methods:{
        pushwish(){
            //检查字数
            if(this.tempcontent.text.wish.length>16){
                alert('请输入小于十七个文字的内容')
                return
            }
            if(this.tempcontent.text.wish.length<=0){
                alert('请输入内容')
                return
            }
            //检查姓名
            if(this.tempcontent.text.username.length>7){
                alert('请输入小于八个文字的姓名')
                return
            }
            if(this.tempcontent.text.username.length<=0){
                alert('请输入姓名')
                return
            }
            //检查图片
            if(this.selection.length<=0){
                alert('请选择心仪大学')
                return
            }
            this.showWishs=false

            //获取时间
            let date=new Date()
            let year=date.getFullYear()
            let month=date.getMonth()
            let dateArr=[
                date.getMonth()+1,
                date.getDate(),
            ];
            for(let i=0;i<dateArr.length;i++){
                if(dateArr[i]>=1&&dateArr[i]<=9){
                    dateArr[i]='0'+dateArr[i];
                }
            }
            this.tempcontent.date=year+'-'+dateArr[0]+'-'+dateArr[1];
            //计算keynum
            this.tempcontent.keynum=this.content.length-2
            //计算keys
            this.tempcontent.keys='--d:'+(++this.tempcontent.keynum)


            //加入此记录
            let temp={
                date:this.tempcontent.date,
                keynum:this.content[this.content.length-1].keynum+1,
                keys:this.tempcontent.keys,
                text:{
                    username:this.tempcontent.text.username,
                    wish:this.tempcontent.text.wish
                },
                value:null
            }
            
            //获取value
            if(this.selection=='郑州大学'){
                temp.value=require('../components/Mainpages/imags/zzu1.jpg')    
            }else if(this.selection=='武汉大学'){
                temp.value=require('../components/Mainpages/imags/wuhan.png')
            }else if(this.selection=='浙江大学'){
                temp.value=require('../components/Mainpages/imags/zhejiang2.png')
            }else if(this.selection=='北京大学'){
                temp.value=require('../components/Mainpages/imags/peking.png')
            }
            this.selection=''
            this.tempcontent.value=''
            this.content.push(temp)
            console.log(this.content)
            let that =this
            setTimeout(() => {
                that.showWishs=true
                that.success=true
            }, 1);
            setTimeout(() => {
                that.success=false
            }, 2000);
        }
    }
    //methods结束
}
</script>
<style scoped>
@import url(https://fonts.googleapis.com/css?family=Roboto:400,600);
.select{
    position: absolute;
    margin-left: 50%;
    top: 24%;
}
.icons{
    position: absolute;
    top: 30px;
    z-index: 100;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background-color: #eee;
    margin-left: 49%;
}
.wishPageroot{
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 752px;
    font-family: "Roboto",sans-serif;
    background: cadetblue;
}
.container{
    display: flex;
    justify-content: center;
}
.comment-box{
    background: #fff;
    width: 85%;
    margin-top: 50px;
    padding: 5px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
}
button{
    color: #fff;
    background: #298d9f;
    border: 0;
    margin: 12px 0 0 10px;
    padding: 12px 20px 12px 20px;
    border-radius: 5px;
    transition: all .2s ease;
}
button:hover{
    background: #175763;
}
button.btn-close{
    background-color: #28256b;
}
.header{
    margin: 15px 20px;
    font-size: 27px;
    font-weight: 600;
}
input[type="text"],
textarea{
    margin: 5px 10px;
    outline: none;
    background: #efefef;
    border: 0;
    padding: 10px;
    border-radius: 5px;
}
textarea{
    width: 85%;
    resize: none;
}
input[type="text"]{
    width: 50%;
    margin-bottom: 15px;
}
h4{
    margin: 10px 30px;
}
hr{
    border: 0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}
span.date{
    font-weight: 300;
    font-size: 12px;
    float: right;
    margin-right: 10px;
}
.comment-box p{
    margin: 0 30px 15px;
    font-weight: 300;
    color: #333;
    background: #eee;
    padding: 30px 15px;
    word-wrap: break-word;
}
textarea:disabled,
[type="text"]:disabled,
button:disabled{
    background-color: gray;
    cursor: not-allowed;
}
button:disabled:hover{
    background-color: gray;
}
</style>
